@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto, sans-serif;
    color: #f0f0f5;
    background: #121212;
    -webkit-font-smoothing: antialiased; 
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input{
    width: 100%;
    height: 60px;
    color: #f0f0f5;
    border: 1px solid #999;
    border-radius: 8px;
    padding: 0 24px;
}

.button {
    width: 100%;
    height: 60px;
    background: #ec602c;
    border: 0;
    border-radius: 8px;
    color: #fff;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    line-height: 60px;
    transition: -webkit-filter 0.3s;
    transition: filter 0.3s;
    transition: filter 0.3s, -webkit-filter 0.3s;
}

.button:hover {
    -webkit-filter: brightness(84%);
            filter: brightness(84%)
}
.home-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}


.home-container section.form{
    width: 100%;
    max-width: 350px;
    margin-right: 50px;
}

.home-container section.form form{
    margin-top: 50px;
}

.form-desc{
    font-size: 24px;
    margin-bottom: 32px;
}

.desc-text{
    font-size: 22px;
    margin-bottom: 32px;
    font: Roboto;
}

.logo-text{
    font-size: 32px;
    margin-bottom: 32px;
    font-family: 'Press Start 2P', cursive;
}


